<template>
  <div class="component-learn">
    <div class="parallax ">
      <div class="wrapper">
        <h1>시작하기</h1>
        <h2>퍼블레싱을 시작하세요! 콘솔을 열고 명령하세요!</h2>
      </div>
    </div>
    <div class="learn">
      <div class="wrapper clearfix">
        <div class="lnb" ref="lnbRef">
          <ul>
            <li class="border-point color-point border-point-hover color-point-hover">시작하기</li>
            <li class="border-point-hover color-point-hover">동영상 매뉴얼</li>
            <li class="border-point-hover color-point-hover">콘솔 명령어</li>
            <li class="border-point-hover color-point-hover">노드 명령어</li>
          </ul>
        </div>
        <div class="content">
          <div class="start part">
            <div class="sm-title">시작하기</div>
            <ul>
              <li>- 다음의 순서에 따라 진행하시면 페이지를 생성하고 추출하실 수 있습니다.</li>
              <li>- 우측 상단의 OPEN CONSOLE 버튼을 클릭합니다.</li>
              <li>- 명령창에 join 입력 후 엔터를 눌러 가입합니다.</li>
              <li>- 명령창에 login 입력 후 엔터를 눌러 로그인합니다.</li>
              <li>- 명령창에 create startup 입력 후 엔터를 눌러 페이지를 생성합니다.</li>
              <li>- 명령창에 open startup 입력 후 엔터를 눌러 생성된 페이지를 오픈합니다.</li>
              <li>- 명령창에 @root append header 100 입력 후 엔터를 눌러 컴포넌트를 추가합니다.</li>
              <li>- 명령창에 @root append slider 100 입력 후 엔터를 눌러 컴포넌트를 추가합니다.</li>
              <li>- 명령창에 @root append footer 100 입력 후 엔터를 눌러 컴포넌트를 추가합니다.</li>
              <li>- 명령창에 export 입력 후 엔터를 눌러 소스를 다운로드합니다.</li>
            </ul>
          </div>
          <div class="manual part">
            <div class="sm-title">동영상 매뉴얼</div>
            <div class="row">
              <div class="on col-9">
                <div class="video">
                  <iframe
                    width="100%"
                    height="100%"
                    title="Video Player"
                    :src="`https://www.youtube.com/embed/${state.video}?list=PLtht1_et-35B58-NHHItPWOuyH6c3YhN0`"
                    frameborder="0"
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                    allowfullscreen
                    ref="videoRef"
                  ></iframe>
                </div>
              </div>
              <div class="list col-3">
                <div class="inner">
                  <ul>
                    <li v-for="(v, idx) in videos" :key="idx" :class="{ active: v === state.video }" @click="state.video = v">
                      <img :src="`https://img.youtube.com/vi/${v}/mqdefault.jpg`" alt="Video thumbnail" />
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
          <div class="commands part">
            <div class="sm-title">콘솔 명령어</div>
            <div class="table-responsive">
              <table class="table">
                <thead>
                  <tr>
                    <th>명령어</th>
                    <th>설명</th>
                    <th>인자 값</th>
                    <th>예제</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(c, idx) in commands.console" :key="idx">
                    <td>{{ c.cmd }}</td>
                    <td>{{ c.desc }}</td>
                    <td>{{ c.args.join(" ") }}</td>
                    <td>{{ c.examples.join("\n") }}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
          <div class="part">
            <div class="sm-title">노드 명령어</div>
            <div class="table-responsive">
              <table class="table">
                <thead>
                  <tr>
                    <th>명령어</th>
                    <th>설명</th>
                    <th>인자 값</th>
                    <th>예제</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(c, idx) in commands.node" :key="idx">
                    <td>{{ c.cmd }}</td>
                    <td>{{ c.desc }}</td>
                    <td>{{ c.args.join(" ") }}</td>
                    <td>{{ c.examples.join("\n") }}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { reactive, ref } from "@vue/reactivity";
import defines from "../defines";
import { onMounted } from "@vue/runtime-core";
export default {
  props: {
    load: Function,
  },
  setup(props) {
    const state = reactive({
      video: "",
    });

    const lnbRef = ref(null);
    const definedCommands = defines.commands;
    const videos = ["55zlvSk_Cd0", "QEhsJZOUhls", "88oE1aLzVaI", "kI6T9ccmnLg", "7Nt7sYy1sS0", "hyq7Yinl-wY", "fM2BlvPuFw4", "J0ca85OEUZ4", "ryphlG9gJZs", "j7ofpvqF5_M", "FUBe-x9Walc", "zxRRzeroV7U"];

    const commands = {
      console: [],
      node: [],
    };

    for (let i1 in definedCommands) {
      for (let i2 in definedCommands[i1]) {
        for (let c of definedCommands[i1][i2]) {
          commands[i1].push(c);
        }
      }
    }

    for (let i in commands) {
      commands[i].sort((a, b) => {
        if (a.cmd < b.cmd) {
          return -1;
        } else if (a.cmd > b.cmd) {
          return 1;
        }

        return 0;
      });
    }

    state.video = videos[0];

    onMounted(() => {
      if (typeof props.load === "function") {
        props.load(lnbRef.value);
      }
    });

    return { state, lnbRef, commands, videos };
  },
};
</script>
