          margin-bottom: 5px;

          &:last-child {
            margin-bottom: 0;
          }
        }
      }
    }

    &.manual {
      .row {
        .on {
          padding-right: 0;
        }

        .list {
          .inner {
            position: relative;
            background: #eee;
            height: 100%;

            ul {
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              overflow: auto;
              padding: 15px;
              margin: 0;
              list-style: none;
              border: 1px solid #ccc;

              > li {
                cursor: pointer;
                margin-bottom: 15px;

                &.active {
                  opacity: 0.5;
                }

                &:last-child {
                  margin-bottom: 0;
                }
              }
            }
          }
        }
      }
    }
  }

  table {
    border-bottom: 1px solid #ddd;

